webpackJsonp([131956398574278],{

/***/ 712:
/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<h1 id=\"gatsby-plugin-sharp\"><a href=\"#gatsby-plugin-sharp\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>gatsby-plugin-sharp</h1>\n<p>Exposes several image processing functions built on the\n<a href=\"https://github.com/lovell/sharp\">Sharp image processing library</a>. This is a\nlow-level helper plugin generally used by other Gatsby plugins. You generally\nshouldn’t be using this directly but might find it helpful if doing very custom\nimage processing.</p>\n<p>It aims to provide excellent out-of-the box settings for processing common web\nimage formats.</p>\n<p>For JPEGs it generates progressive images with a default quality level of 50.</p>\n<p>For PNGs it uses <a href=\"https://github.com/pornel/pngquant\">pngquant</a> to compress\nimages. By default it uses a quality setting of [50-75].</p>\n<h2 id=\"install\"><a href=\"#install\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Install</h2>\n<p><code>npm install --save gatsby-plugin-sharp</code></p>\n<h2 id=\"how-to-use\"><a href=\"#how-to-use\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>How to use</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token comment\">// In your gatsby-config.js</span>\nplugins<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span><span class=\"token template-string\"><span class=\"token string\">`gatsby-plugin-sharp`</span></span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<h2 id=\"methods\"><a href=\"#methods\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Methods</h2>\n<h3 id=\"resize\"><a href=\"#resize\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>resize</h3>\n<h4 id=\"parameters\"><a href=\"#parameters\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Parameters</h4>\n<ul>\n<li><code>width</code> (int, default: 400)</li>\n<li><code>height</code> (int)</li>\n<li><code>quality</code> (int, default: 50)</li>\n<li><code>jpegProgressive</code> (bool, default: true)</li>\n<li><code>pngCompressionLevel</code> (int, default: 9)</li>\n<li><code>base64</code>(bool, default: false)</li>\n</ul>\n<h4 id=\"returns\"><a href=\"#returns\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Returns</h4>\n<ul>\n<li><code>src</code> (string)</li>\n<li><code>width</code> (int)</li>\n<li><code>height</code> (int)</li>\n<li><code>aspectRatio</code> (float)</li>\n</ul>\n<h3 id=\"responsiveresolution\"><a href=\"#responsiveresolution\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>responsiveResolution</h3>\n<p>Automatically create sizes for different resolutions — we do 1x, 1.5x, 2x, and\n3x.</p>\n<h4 id=\"parameters-1\"><a href=\"#parameters-1\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Parameters</h4>\n<ul>\n<li><code>width</code> (int, default: 400)</li>\n<li><code>height</code> (int)</li>\n<li><code>quality</code> (int, default: 50)</li>\n</ul>\n<h4 id=\"returns-1\"><a href=\"#returns-1\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Returns</h4>\n<ul>\n<li><code>base64</code> (string)</li>\n<li><code>aspectRatio</code> (float)</li>\n<li><code>width</code> (float)</li>\n<li><code>height</code> (float)</li>\n<li><code>src</code> (string)</li>\n<li><code>srcSet</code> (string)</li>\n</ul>\n<h3 id=\"responsivesizes\"><a href=\"#responsivesizes\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>responsiveSizes</h3>\n<p>Create sizes (in width) for the image. If the max width of the container for the\nrendered markdown file is 800px, the sizes would then be: 200, 400, 800, 1200,\n1600, 2400 – enough to provide close to the optimal image size for every device\nsize / screen resolution.</p>\n<p>On top of that, responsiveSizes returns everything else (namely aspectRatio and\na base64 image to use as a placeholder) you need to implement the “blur up”\ntechnique popularized by Medium and Facebook (and also available as a Gatsby\nplugin for Markdown content as gatsby-remark-images).</p>\n<h4 id=\"parameters-2\"><a href=\"#parameters-2\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Parameters</h4>\n<ul>\n<li><code>maxWidth</code> (int, default: 800)</li>\n<li><code>maxHeight</code> (int)</li>\n<li><code>quality</code> (int, default: 50)</li>\n<li><code>sizeByPixelDensity</code> (bool, default: false)</li>\n</ul>\n<h4 id=\"returns-2\"><a href=\"#returns-2\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Returns</h4>\n<ul>\n<li><code>base64</code> (string)</li>\n<li><code>aspectRatio</code> (float)</li>\n<li><code>src</code> (string)</li>\n<li><code>srcSet</code> (string)</li>\n<li><code>sizes</code> (string)</li>\n<li><code>originalImg</code> (string)</li>\n</ul>\n<h3 id=\"shared-options\"><a href=\"#shared-options\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Shared Options</h3>\n<p>In addition to their individual parameters, all methods above share the\nfollowing:</p>\n<ul>\n<li><code>grayscale</code> (bool, default: false)</li>\n<li><code>duotone</code> (bool|obj, default: false)</li>\n<li><code>toFormat</code> (string, default: ”)</li>\n<li><code>cropFocus</code> (string, default: ’<a href=\"http://sharp.dimens.io/en/stable/api-resize/#crop\">sharp.strategy.attention</a>‘)</li>\n</ul>\n<h4 id=\"toformat\"><a href=\"#toformat\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>toFormat</h4>\n<p>Convert the source image to one of the following available options: <code>NO_CHANGE</code>,\n<code>JPG</code>, <code>PNG</code>, <code>WEBP</code>.</p>\n<h4 id=\"cropfocus\"><a href=\"#cropfocus\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>cropFocus</h4>\n<p>Change the cropping focus. Available options: <code>CENTER</code>, <code>NORTH</code>, <code>NORTHEAST</code>,\n<code>EAST</code>, <code>SOUTHEAST</code>, <code>SOUTH</code>, <code>SOUTHWEST</code>, <code>WEST</code>, <code>NORTHWEST</code>, <code>ENTROPY</code>,\n<code>ATTENTION</code>. See Sharp’s <a href=\"http://sharp.dimens.io/en/stable/api-resize/#crop\">crop</a>.</p>\n<h4 id=\"rotate\"><a href=\"#rotate\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>rotate</h4>\n<p>Rotate the image (after cropping). See Sharp’s <a href=\"http://sharp.dimens.io/en/stable/api-operation/#rotate\">rotate</a>.</p>\n<h4 id=\"grayscale\"><a href=\"#grayscale\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>grayscale</h4>\n<p>Uses Sharp’s <a href=\"http://sharp.dimens.io/en/stable/api-colour/#greyscale\">greyscale</a> to convert the source image to 8-bit greyscale, 256\nshades of grey, e.g.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>allImageSharp <span class=\"token punctuation\">{</span>\n  edges <span class=\"token punctuation\">{</span>\n    node <span class=\"token punctuation\">{</span>\n      <span class=\"token operator\">...</span> on ImageSharp <span class=\"token punctuation\">{</span>\n        <span class=\"token function\">resize</span><span class=\"token punctuation\">(</span>width<span class=\"token punctuation\">:</span> <span class=\"token number\">150</span><span class=\"token punctuation\">,</span> height<span class=\"token punctuation\">:</span> <span class=\"token number\">150</span><span class=\"token punctuation\">,</span> grayscale<span class=\"token punctuation\">:</span> <span class=\"token boolean\">true</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n          src\n        <span class=\"token punctuation\">}</span>\n      <span class=\"token punctuation\">}</span>\n    <span class=\"token punctuation\">}</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h4 id=\"duotone\"><a href=\"#duotone\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>duotone</h4>\n<p>Applys a “duotone” effect (see <a href=\"https://alistapart.com/article/finessing-fecolormatrix\">I</a>, <a href=\"http://blog.72lions.com/blog/2015/7/7/duotone-in-js\">II</a>, <a href=\"https://ines.io/blog/dynamic-duotone-svg-jade\">III</a>) to the source image if\ngiven two hex colors <code>shadow</code> and <code>highlight</code> defining start and end color of\nthe duotone gradient, e.g.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token function\">responsiveResolution</span><span class=\"token punctuation\">(</span>\n  width<span class=\"token punctuation\">:</span> <span class=\"token number\">800</span><span class=\"token punctuation\">,</span>\n  duotone<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n    highlight<span class=\"token punctuation\">:</span> <span class=\"token string\">\"#f00e2e\"</span><span class=\"token punctuation\">,</span>\n    shadow<span class=\"token punctuation\">:</span> <span class=\"token string\">\"#192550\"</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  src\n  srcSet\n  base64\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>the source image colors will be converted to match a gradient color chosen based\non each pixel’s <a href=\"https://en.wikipedia.org/wiki/Relative_luminance\">relative luminance</a>.<br>\nLogic is borrowed from <a href=\"https://github.com/nagelflorian/react-duotone\">react-duotone</a>.</p>\n<p>You can pass a third optional parameter, <code>opacity</code>:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token function\">responsiveResolution</span><span class=\"token punctuation\">(</span>\n  width<span class=\"token punctuation\">:</span> <span class=\"token number\">800</span><span class=\"token punctuation\">,</span>\n  duotone<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n    highlight<span class=\"token punctuation\">:</span> <span class=\"token string\">\"#f00e2e\"</span><span class=\"token punctuation\">,</span>\n    shadow<span class=\"token punctuation\">:</span> <span class=\"token string\">\"#192550\"</span><span class=\"token punctuation\">,</span>\n    opacity<span class=\"token punctuation\">:</span> <span class=\"token number\">50</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  src\n  srcSet\n  base64\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>If set, a semi-transparent version of duotone’d image will be composited over\nthe original image, allowing the original image and its colors to partially\n“shine through”. <em>Heads up</em>: If the original image contains an alpha channel it\nwill be <a href=\"http://sharp.dimens.io/en/stable/api-operation/#flatten\">flattened</a> before creating the composite.</p>\n<p>This works by adding an alpha channel to the duotone’d image - then we let Sharp\ndo its magic via\n<a href=\"http://sharp.dimens.io/en/stable/api-composite/#overlaywith\"><code>overlayWith</code></a>;\nquoting the Sharp documentation:</p>\n<blockquote>\n<p>If the overlay image contains an alpha channel then composition with\n<a href=\"https://en.wikipedia.org/wiki/Alpha_compositing\">premultiplication</a>\nwill occur.</p>\n</blockquote>\n<h4 id=\"tracedsvg\"><a href=\"#tracedsvg\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>tracedSVG</h4>\n<p>Generates a traced SVG of the image (see <a href=\"https://github.com/gatsbyjs/gatsby/issues/2435\">the original GitHub issue</a>) and\nreturns the SVG as ”<a href=\"https://codepen.io/tigt/post/optimizing-svgs-in-data-uris\">optimized URL-encoded</a>” <code>data:</code> URI. It it used in\n<a href=\"/packages/gatsby-image/\">gatsby-image</a> to provide an\nalternative to the default inline base64 placeholder image.</p>\n<p>Uses <a href=\"https://github.com/tooolbox/node-potrace\">node-potrace</a> and <a href=\"https://github.com/svg/svgo\">SVGO</a> under the hood. Default settings for\nnode-potrace:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code>  <span class=\"token punctuation\">{</span>\n    color<span class=\"token punctuation\">:</span> <span class=\"token template-string\"><span class=\"token string\">`lightgray`</span></span><span class=\"token punctuation\">,</span>\n    optTolerance<span class=\"token punctuation\">:</span> <span class=\"token number\">0.4</span><span class=\"token punctuation\">,</span>\n    turdSize<span class=\"token punctuation\">:</span> <span class=\"token number\">100</span><span class=\"token punctuation\">,</span>\n    turnPolicy<span class=\"token punctuation\">:</span> TURNPOLICY_MAJORITY<span class=\"token punctuation\">,</span>\n  <span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>All <a href=\"https://github.com/tooolbox/node-potrace#parameters\">node-potrace <code>Potrace</code> parameters</a> are exposed and can be set via the\n<code>traceSVG</code> argument:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token function\">responsiveResolution</span><span class=\"token punctuation\">(</span>\n  traceSVG<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n    color<span class=\"token punctuation\">:</span> <span class=\"token string\">\"#f00e2e\"</span>\n    turnPolicy<span class=\"token punctuation\">:</span> TURNPOLICY_MINORITY\n    blackOnWhite<span class=\"token punctuation\">:</span> <span class=\"token boolean\">false</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  src\n  srcSet\n  tracedSVG\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>","excerpt":"gatsby-plugin-sharp Exposes several image processing functions built on the\n Sharp image processing library . This is a\nlow-level helper…","timeToRead":5,"fields":{"title":"gatsby-plugin-sharp"},"parent":{"__typename":"File","relativePath":"gatsby-plugin-sharp/README.md"}}},"pathContext":{"slug":"/packages/gatsby-plugin-sharp/"}}

/***/ })

});


